.DayPicker {
  display: inline-block;
  height: 100%;
  border: 1px solid var(--input-border);
}

.DayPicker input {
  text-align: center;
  height: 100%;
  border: none;
  outline: none !important;
}

.DayPicker button {
  border: none;
  outline: none;
  cursor: pointer !important;
  line-height: normal !important;
  height: 100%;
  padding: 0px 10px;
  background-color: #f5f7f7;
  color: var(--dark);
}

.DayPicker button:hover,
.DayPicker button:active {
  color: var(--primary);
}

.rdp-caption_label {
  text-transform: capitalize !important;
}

.rdp-dropdown_icon {
  display: none !important;
}

.rdp-dropdown:focus:not([disabled]) + .rdp-caption_label,
.rdp-dropdown:active:not([disabled]) + .rdp-caption_label {
  border: none !important;
  background-color: var(--white) !important;
}

.dialog-sheet .rdp-dropdown_month:nth-child(2) {
  margin-left: 10px !important;
  margin-right: 10px !important;
}

.rdp-dropdown_month {
  padding: 0px 17px !important;
}

.rdp-caption .rdp-button:focus:not([disabled]),
.rdp-caption .rdp-button:active:not([disabled]),
.rdp-caption .rdp-button:hover:not([disabled]) {
  background-color: var(--white) !important;
  border: none !important;
  outline: none !important;
}

.rdp-caption .rdp-button:hover:not([disabled]) {
  color: var(--close-modal) !important;
}

.rdp-day_today:not([disabled]) {
  color: inherit !important;
  background-color: var(--white) !important;
  border: none !important;
  font-weight: 800;
}

.rdp-day_selected:not([disabled]) {
  background-color: var(--primary) !important;
  border: none !important;
  color: var(--white);
}

.rdp-day:hover:not([disabled]) {
  background-color: var(--primary-hover) !important;
}

.rdp-day:not([disabled]) {
  border: none !important;
}

.rdp-day_disabled {
  color: #dce0e0;
}
